.hero__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-areas:
    "card photos";
  gap: var(--gap);
  padding-top: 41px;
  padding-bottom: 68px;

  // --- max-width: 1024px ---
  @include tablet {
    padding-top: 34px;
    padding-bottom: 68px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding-top: 35px;
  }

  // --- max-width: 576px ---
  @include mobile {
    grid-template-columns: 1fr;
    grid-template-rows: minmax(422px, 1fr) auto;
    grid-template-areas:
    "card"
    "photos";
    gap: 35px;
    padding-top: 30px;
    padding-bottom: 49px;
  }
}

// --- MAIN CARD ---

.hero__main-card {
  grid-area: card;
}

.main-card {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: flex-start;
  padding: 15px;
  height: 100%;
  background-image: url("../img/1920_hero-main.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  // --- max-width: 1024px ---
  @include tablet {
    background-image: url("../img/1024_hero-main.jpg");
  }

  // --- max-width: 768px ---
  @include small-tablet {
    background-image: url("../img/768_hero-main.jpg");
  }

  // --- max-width: 576px ---
  @include mobile {
    padding: 5px;
    background-image: url("../img/320_hero-main.jpg");
  }
}

.main-card__theme {
  @include theme-label;
  align-self: flex-end;

  // --- max-width: 576px ---
  @include mobile {
    padding: 5px 7px;
  }
}

.main-card__wrap {
  padding: 45px;

  // --- max-width: 1024px ---
  @include tablet {
    padding: 15px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding: 25px;
  }
}

.main-card__descr {
  margin-bottom: 3px;
  font-size: 16px;
  font-weight: 400;
  color: var(--light-color);

  // --- max-width: 1024px ---
  @include tablet {
    margin-bottom: 8px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    margin-bottom: 13px;
  }

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 8px;
    font-size: 14px;
  }
}

.main-card__title {
  font-family: var(--second-font-family);
  font-size: 36px;
  font-weight: 700;
  line-height: 37px;
  color: var(--light-color);

  // --- max-width: 1024px ---
  @include tablet {
    margin-bottom: 0;
    font-size: 34px;
    line-height: 35px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    margin-bottom: 2px;
    line-height: 90%;
  }

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 0;
    font-size: 24px;
    line-height: 110%;
  }
}

.main-card__time {
  display: block;
  margin-bottom: 20px;
  font-family: var(--second-font-family);
  font-size: 36px;
  font-weight: 700;
  line-height: 37px;
  color: var(--light-color);

  // --- max-width: 1024px ---
  @include tablet {
    margin-bottom: 22px;
    font-size: 34px;
    line-height: 35px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    margin-bottom: 24px;
    line-height: 90%;
  }

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 16px;
    font-size: 24px;
    line-height: 110%;
  }
}

.main-card__btn {
  @include btn-filled;
  display: inline-block;
}

// --- PREVIOUS WORKS ---

.hero__previous {
  grid-area: photos;
}

.previous__title {
  @include section-title;
  margin-bottom: 18px;
  color: var(--dark-color);
  line-height: 25px;

  // --- max-width: 1024px ---
  @include tablet {
    margin-bottom: 25px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    margin-bottom: 16px;
    font-size: 22px;
    line-height: 23px;
  }

  // --- max-width: 576px ---
  @include mobile {
    font-size: 18px;
    font-weight: 700;
    line-height: 104%;
  }
}

.previous__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: var(--gap);
}

.previous__item:nth-child(1) {
  grid-column: span 2;
}
