.header {
  background-color: var(--dark-color);
}

.header__container {
  position: relative;
  display: grid;
  grid-template-areas: "logo menu button";
  grid-template-columns: 3fr 7fr 217px;
  align-items: center;
  padding-top: 24px;
  padding-bottom: 21px;

  // --- max-width: 1315px ---
  @include big-desktop {
    grid-template-areas:
      "logo logo button"
      "menu menu menu";
    grid-template-rows: repeat(2, 1fr);
  }

  // --- max-width: 1024px ---
  @include tablet {
    grid-template-areas: "logo menu menu button";
    grid-template-columns: 166px 2fr 2fr 155px;
    grid-template-rows: unset;
    padding-top: 25px;
    padding-bottom: 23px;
  }

  // --- max-width: 960px ---
  @include in-between-tablet {
    grid-template-areas: "logo button";
    grid-template-columns: 166px 1fr;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding-top: 24px;
  }
}

// --- MAIN LOGO ---

.header__logo-wrap {
  grid-area: logo;
}

.header__logo {
  @include logo;
  transition: visibility 0.3s ease-in-out,
    opacity 0.3s ease-in-out;

  // --- max-width: 1024px ---
  @include tablet {
    transform: translateY(2px);
  }
}

// --- MENU ---

.header__menu-wrap {
  background-color: var(--about-section-input);
}

.header__nav {
  justify-self: right;
  grid-area: menu;

  // --- max-width: 1315px ---
  @include big-desktop {
    justify-self: left;
    padding-top: 20px;
  }

  // --- max-width: 1024px ---
  @include tablet {
    justify-self: right;
    padding-top: 0;
  }

  // --- max-width: 960px ---
  @include in-between-tablet {
    display: none;
  }
}

.nav__list {
  display: flex;
  gap: 75px;

  // --- max-width: 1024px ---
  @include tablet {
    gap: 29px;
  }
}

.nav__link {
  padding: 0 2px;
  font-family: var(--second-font-family);
  font-size: 16px;
  font-weight: 400;
  color: var(--light-color);
  transition: color 0.5s ease-in-out,
    background-color 0.5s ease-in-out;
}

.nav__link:focus-visible {
  color: var(--light-color);
  background-color: var(--brown-color);
}

@media (any-hover: hover) {
  .nav__link:hover {
    color: var(--orange-color);
  }
}


.nav__link:active {
  color: var(--orange-active-color);
}

// --- SEARCH BUTTON ---

.header__btn {
  grid-area: button;
  justify-self: right;
  width: 26px;
  height: 26px;
  transform: translateY(1px);
  transition: visibility 0.5s ease-in-out,
    opacity 0.5s ease-in-out,
    transform 0.5s ease-in-out;

  // --- max-width: 768px ---
  @include small-tablet {
    transform: translateY(-2px);
  }

  // --- max-width: 576px ---
  @include mobile {
    width: 20px;
    height: 20px;
    transform: translateY(0px);
  }
}

.header__btn-icon {
  width: 100%;
  height: 100%;
  fill: var(--light-color);
  transition: fill 0.5s ease-in-out;
}

.header__btn:focus-visible .header__btn-icon {
  fill: var(--orange-color);
}

@media (any-hover: hover) {
  .header__btn:hover .header__btn-icon {
    fill: var(--orange-color);
  }
}

.header__btn:active .header__btn-icon {
  fill: var(--orange-active-color);
}

// --- SEARCH FORM ---

.search {
  display: flex;
  justify-content: right;
  position: absolute;
  top: 25px;
  right: 15px;
  transform: translateX(100px);
  transition: visibility 0.5s ease-in-out,
    opacity 0.5s ease-in-out,
    transform 0.5s ease-in-out;

  // --- max-width: 1315px ---
  @include big-desktop {
    top: 27px;
  }

  // --- max-width: 1024px ---
  @include tablet {
    top: 26px;
    right: 31px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    right: 27px;
  }

  // --- max-width: 576px ---
  @include mobile {
    top: 21px;
    right: 21px;
    width: 100%;
    max-width: 278px;
  }
}

.search__label {

  // --- max-width: 576px ---
  @include mobile {
    width: 100%;
  }
}

.search__input {
  width: 170px;
  padding-top: 10px;
  color: var(--light-color);
  border-bottom: 1px solid var(--pale-color);
  background-color: var(--dark-color);
  transition: border-bottom-color 0.5s ease-in-out;

  // --- max-width: 1024px ---
  @include tablet {
    width: 205px;
  }

  // --- max-width: 576px ---
  @include mobile {
    width: 100%;
  }
}

.search__input::placeholder {
  font-family: var(--second-font-family);
  font-size: 16px;
  font-weight: 400;
  color: var(--pale-color);
  transition: color 0.5s ease-in-out;
}

.search__input:focus-visible {
  border-bottom-color: var(--orange-active-color);
}

@media (any-hover: hover) {
  .search__input:hover {
    border-bottom-color: var(--orange-color);
  }
}

.search__btn {
  @include btn-cross;
  transform: translateY(5px);

  // --- max-width: 1024px ---
  @include tablet {
    transform: translateY(3px);
  }

  // --- max-width: 576px ---
  @include mobile {
    transform: translate(-1px, 3px);
  }
}

.search__icon {
  width: 23px;
  height: 23px;

  // --- max-width: 576px ---
  @include mobile {
    width: 20px;
    height: 20px;
  }
}

// --- classes for opening search form ---

.header__btn--hidden {
  transform: translateX(100px);
}

.search-active {
  transform: translateX(0);
}
