.shake {
  animation: shake 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55) both;
}

@keyframes shake {
  10%, 90% {
    transform: translateX(-0.5px);
   }

  20%, 80% {
    transform: translateX(1px);
  }

  30%, 50%, 70% {
    transform: translateX(-2px);
  }

  40%, 60% {
    transform: translateX(2px);
  }
}
