:root {
  // --- base ---
  --font-family: "Roboto", sans-serif;
  --second-font-family: "TTFirsNeue", sans-serif;
  --content-width: 1290px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // --- colors ---
  --light-color: #fff;
  --dark-color: #202020;
  --orange-color: #ff6e30;
  --orange-active-color: #ce4307;
  --brown-color: #bc572b;
  --pale-color: #e9e9e9;
  --about-section-text: #cacaca;
  --about-section-input: #4a4a4a;
  --calendar-color: #999;
  --link-text-focus: #ffd4c1;

  // --- grid gap ---
  --gap: 30px;

  // --- section title ---
  --st-font-family: var(--second-font-family);
  --st-font-weight: 700;
  --st-font-size: 24px;
}
