.contacts__container {
  padding-top: 70px;
  padding-bottom: 62px;

  // --- max-width: 1024px ---
  @include tablet {
    padding-bottom: 60px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding-top: 77px;
    padding-bottom: 52px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding-top: 35px;
    padding-bottom: 47px;
  }
}

.contacts__title {
  @include section-title;
  margin-bottom: 24px;

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 15px;
  }
}

.contacts__wrap {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: calc(var(--gap) + 16px);

  // --- max-width: 1024px ---
  @include tablet {
    grid-template-columns: 2fr minmax(342px, 1fr);
    gap: var(--gap);
  }

  // --- max-width: 960px ---
  @include in-between-tablet {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, 1fr);
    gap: 24px;
  }

  // --- max-width: 576px ---
  @include mobile {
    display: flex;
    flex-direction: column;
  }
}

// --- all styles for map and contacts section are in map.scss file ---

// --- FEEDBACK SECTION ---

.contacts__feedback {
  max-width: 410px;
  transform: translateY(-7px);

  // --- max-width: 1024px ---
  @include tablet {
    justify-self: right;
    max-width: 342px;
  }

  // --- max-width: 960px ---
  @include in-between-tablet {
    justify-self: left;
    max-width: 466px;
    transform: translateY(0px);
  }
}

.feedback__title {
  margin-bottom: 6px;
  font-weight: 400;
  font-size: 24px;
  font-family: var(--second-font-family);
  line-height: 22px;
  color: var(--dark-color);

  // --- max-width: 576px ---
  @include mobile {
    font-size: 18px;
    line-height: 122%;
  }
}

.feedback__descr {
  margin-bottom: 13px;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: var(--calendar-color);

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 16px;
    font-size: 14px;
    line-height: 129%;
  }
}

// --- all styles for FORM are in form.scss file ---
