html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

.site-container {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);

  // --- max-width: 1024px ---
  @include tablet {
    --container-offset: 31px;
    --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  }

  // --- max-width: 768px ---
  @include small-tablet {
    --container-offset: 27px;
  }

  // --- max-width: 576px ---
  @include mobile {
    --container-offset: 21px;
  }
}

.img-style {
  display: block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

// --- RESET STYLES ---

.title-reset {
  margin: 0;
}

.descr-reset {
  margin: 0;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  outline: none;
  cursor: pointer;
}

.link-reset {
  color: inherit;
  text-decoration: none;
  outline: none;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.input-reset {
  padding: 0;
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: transparent;
  outline: none;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

// --- STYLES FOR HIDE ANYTHING ---

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.hide {
  visibility: hidden;
  opacity: 0;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

// ---

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}
