// --- MENU FOR TABLET AND MOBILE ---

.tablet-menu {
  display: none;
  background-color: var(--about-section-input);

  // --- max-width: 960px ---
  @include in-between-tablet {
    display: block;
  }
}

.tablet-menu__container {
  padding-top: 12px;
  padding-bottom: 12px;

  // --- max-width: 768px ---
  @include small-tablet {
    padding-top: 13px;
    padding-bottom: 13px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding-top: 14px;
    padding-bottom: 14px;
  }
}

// --- WRAPPER FOR MOBILE MENU ---

.tablet-menu__wrap {

  // --- max-width: 576px ---
  @include mobile {
    position: absolute;
    top: 67px;
    left: -100%;
    width: 100%;
    height: 100vh;
    padding: 14px 21px 0 21px;
    overflow-y: auto;
    visibility: hidden;
    opacity: 0;
    background-color: var(--dark-color);
    transition: transform 0.7s ease-in-out,
      visibility 0.7s ease-in-out,
      opacity 0.7s ease-in-out;
  }
}

// --- class for opening mobile menu ---

.tablet-menu__wrap--active {
  visibility: visible;
  opacity: 1;
  transform: translateX(100%);
}

// --- BURGER BUTTON ---

.burger {
  @include burger;
  display: none;

  // --- max-width: 576px ---
  @include mobile {
    display: block;
  }
}

.burger-closure {
  @include btn-cross;
  display: none;
  margin-bottom: 16px;

  // --- max-width: 576px ---
  @include mobile {
    display: block;
  }
}

.burger-closure__icon {
  width: 18px;
  height: 18px;
}

// --- PAGE NAVIGATION IN MOBILE MENU ---

.tablet-menu__nav {

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 40px;
  }
}

.tablet-menu__list {
  justify-content: space-between;
  gap: 0;

  // --- max-width: 576px ---
  @include mobile {
    flex-direction: column;
    gap: var(--gap);
  }
}

// --- PHONE CONTACT IN BURGER MENU ---

.tablet-menu__contact {
  @include phone-link;
  display: none;

  // --- max-width: 576px ---
  @include mobile {
    display: inline-block;
  }
}
