// --- COMMON STYLES ---

.form {
  display: flex;
  width: 100%;
}

.form__label {
  position: relative;
  width: 100%;
}

.form__input {
  width: 100%;
  padding: 15px 23px;
  transition: border-color 0.5s ease-in-out;

  // --- max-width: 576px ---
  @include mobile {
    padding: 10px 23px;
  }
}

.form__input::placeholder {
  font-size: 16px;
  font-weight: 400;

  // --- max-width: 576px ---
  @include mobile {
    font-size: 14px;
  }
}

.form__input:focus-visible {
  border-color: var(--orange-color);
}

@media (any-hover: hover) {
  .form__input:hover::placeholder {
    color: var(--orange-color);
  }
}

.form__btn {
  @include btn-filled;
}

// --- SUBSCRIPTION SECTION ---

.subscription__form {
  gap: var(--gap);

  // --- max-width: 1024px ---
  @include tablet {
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
  }

  // --- max-width: 576px ---
  @include mobile {
    gap: 25px;
  }
}

.subscription__form-input {
  color: var(--light-color);
  background-color: var(--about-section-input);
  border: 1px solid var(--about-section-input);
  transition: border-color 0.5s ease-in-out;

  // --- max-width: 1024px ---
  @include tablet {
    max-width: 342px;
  }
}

.subscription__form-input::placeholder {
  color: var(--about-section-text);
  transition: color 0.5s ease-in-out;
}

.subscription__form-input:not(:placeholder-shown) {
  border-color: var(--about-section-input);
}

// --- FEEDBACK SECTION ---

.feedback__form {
  flex-direction: column;
  align-items: flex-start;
  gap: 19px;

  // --- max-width: 576px ---
  @include mobile {
    gap: 20px;
  }
}

.feedback__input {
  background-color: var(--pale-color);
  border: 1px solid var(--pale-color);
  transition: border-color 0.5s ease-in-out;
}

.feedback__input::placeholder {
  color: var(--calendar-color);
  transition: color 0.5s ease-in-out;
}

.feedback__input:not(:placeholder-shown) {
  border-color: var(--pale-color);
}

.feedback__input--message {
  height: 150px;
  resize: none;
}

.form__btn {

  // --- max-width: 576px ---
  @include mobile {
    transform: translateY(-4px);
  }
}

// --- ERROR STYLES ---

.just-validate-error-label {
  position: absolute;
  top: 3px;
  left: 23px;
  font-size: 12px;
  font-weight: 400;

  // --- max-width: 576px ---
  @include mobile {
    top: 2px;
    left: 24px;
    font-size: 10px;
  }
}

// --- error in subscription section ---

.subscription__form .just-validate-error-label {
  color: #f06666 !important;
}

.subscription__form-input.just-validate-error-field {
  border-color: #f06666 !important;
}

// --- error in feedback section ---

.feedback__form .just-validate-error-label {
  color: #ff3030 !important;
}

.feedback__input.just-validate-error-field {
  border-color: #ff3030 !important;
}
