// --- MAP SECTION ---

.contacts__map {
  position: relative;

  // --- max-width: 576px ---
  @include mobile {
    left: 50%;
    right: -37%;
    margin-left: -50vw;
    margin-right: -50vw;
    width: 100vw;
  }
}

.map {
  max-width: 850px;
  height: 460px;

  // --- max-width: 1024px ---
  @include tablet {
    max-width: 590px;
  }

  // --- max-width: 960px ---
  @include in-between-tablet {
    max-width: 100%;
  }

  // --- max-width: 576px ---
  @include mobile {
    min-height: 472px;
    max-width: unset;
    width: 100vw;
  }
}

.ymaps-2-1-79-ground-pane {
  filter: grayscale(1);
}

.ymaps-2-1-79-copyrights-pane {
  display: none;
}

.ymaps-2-1-79-map {
  width: 850px;
  height: 460px;

  // --- max-width: 1024px ---
  @include tablet {
    width: 590px;
  }

  // --- max-width: 960px ---
  @include in-between-tablet {
    width: 100%;
  }

  // --- max-width: 576px ---
  @include mobile {
    width: 100vw;
  }
}

.ymaps-2-1-79-inner-panes {

  // --- max-width: 576px ---
  @include mobile {
    width: 100vw;
  }
}

// --- BLOCK WITH ADDRESS ---

.contacts__address {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;

  // --- max-width: 1024px ---
  @include tablet {
    top: unset;
    bottom: 0;
  }
}

.address {
  align-items: center;
  display: flex;
  justify-content: center;
  max-width: 410px;
  height: 100%;
  padding: 0 53px 0 58px;
  background: rgba(32, 32, 32, 0.85);
  visibility: hidden;
  opacity: 0;
  transform: scale(0.1);
  transform-origin: left top;
  transition: transform 0.5s ease-in-out,
    visibility 0.5s ease-in-out,
    opacity 0.5s ease-in-out;

  // --- max-width: 1024px ---
  @include tablet {
    justify-content: left;
    height: unset;
    max-width: unset;
    width: 100%;
    padding: 0 30px;
    transform-origin: left bottom;
  }

  // --- max-width: 576px ---
  @include mobile {
    width: 100vw;
    padding: 0 18px;
  }
}

// --- class for opening address block ---

.address--active {
  visibility: visible;
  opacity: 1;
  transform: scale(1);
  transition: transform 0.5s ease-in-out,
    visibility 0.5s ease-in-out,
    opacity 0.5s ease-in-out;
}

// --- styles of inner address block elements ---

.contacts__btn {
  @include btn-cross;
  position: absolute;
  top: 19px;
  right: 20px;
}

.contacts__btn-icon {
  width: 20px;
  height: 20px;
}

.address__wrap {
  max-width: 300px;
  padding-top: 24px;

  // --- max-width: 1024px ---
  @include tablet {
    padding-bottom: 23px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    max-width: 430px;
    padding-top: 24px;
  }

  // --- max-width: 576px ---
  @include mobile {
    max-width: 268px;
    padding-top: 25px;
    padding-bottom: 11px;
  }
}

.address__title {
  margin-bottom: 5px;
  font-family: var(--second-font-family);
  font-size: 24px;
  font-weight: 400;
  line-height: 22px;
  color: var(--light-color);

  // --- max-width: 768px ---
  @include small-tablet {
    margin-bottom: 6px;
  }

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 12px;
    font-size: 18px;
    line-height: 122%;
  }
}

.address__descr {
  margin-bottom: 2px;
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
  font-style: normal;
  color: var(--about-section-text);

  // --- max-width: 1024px ---
  @include tablet {
    margin-bottom: 0;
  }

  // --- max-width: 960px ---
  @include in-between-tablet {
    margin-bottom: 2px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    margin-bottom: 0;
  }

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 7px;
    font-size: 14px;
    line-height: 129%;
  }
}

.address__link {
  @include phone-link;
}

// --- HIDDEN BLOCK ---

.contacts__hidden-block {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  width: 35px;
  height: 35px;
  background-color: rgba(32, 32, 32, 0.85);
  transition: visibility 0.4s ease-in-out,
    opacity 0.4s ease-in-out;

  // --- max-width: 1024px ---
  @include tablet {
    top: unset;
    bottom: 0;
  }
}

.contacts__btn-logo {
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 0;
  transform: translate(-50%, -50%);
}

.contacts__icon-logo {
  width: 26px;
  height: 25px;
}

.contacts__icon-logo path {
  transition: fill 0.5s ease-in-out;
}

.contacts__btn-logo:focus-visible path {
  fill: var(--orange-active-color);
}

@media (any-hover: hover) {
  .contacts__btn-logo:hover path {
    fill: var(--orange-active-color);
  }
}

