.about {
  background-color: var(--dark-color);
}

.about__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: var(--gap);
  padding-top: 32px;
  padding-bottom: 71px;

  // --- max-width: 1024px ---
  @include tablet {
    padding-bottom: 89px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    padding-bottom: 62px;
  }

  // --- max-width: 688px ---
  @include in-between-small-tablet {
    grid-template-columns: 1fr;
    gap: 32px;
    padding-top: 34px;
    padding-bottom: 48px;
  }
}

// --- TEXT ---

.about__title {
  @include section-title;
  margin-bottom: 6px;
  color: var(--light-color);
}

.about__descr {
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
  color: var(--about-section-text);

  // --- max-width: 576px ---
  @include mobile {
    font-size: 14px;
    line-height: 178%;
  }
}

// --- SUBSCRIPTION ---

.subscription {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 25px;

  // --- max-width: 1024px ---
  @include tablet {
    transform: translateY(7px);
  }

  // --- max-width: 768px ---
  @include small-tablet {
    transform: translateY(-8px);
  }

  // --- max-width: 576px ---
  @include mobile {
    gap: 26px;
    transform: translateY(-8px);
  }
}

.about__background {
  max-width: 100%;
  min-height: 255px;
  background-image: url("../img/1920_about.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  // --- max-width: 1024px ---
  @include tablet {
    min-height: 332px;
    background-image: url("../img/1024_about.jpg");
  }

   // --- max-width: 768px ---
   @include small-tablet {
    min-height: 569px;
    background-image: url("../img/768_about.jpg");
  }

  // --- max-width: 688px ---
  @include in-between-small-tablet {
    min-height: 278px;
    background-image: url("../img/1920_about.jpg");
  }

  // --- max-width: 576px ---
  @include mobile {
    background-image: url("../img/320_about.jpg");
  }
}

.subscription__title {
  margin-bottom: 16px;
  font-family: var(--second-font-family);
  font-size: 24px;
  font-weight: 400;
  line-height: 22px;
  color: var(--light-color);

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 14px;
    font-size: 18px;
  }
}

