.projects__container {
  padding-top: 69px;

  // --- max-width: 768px ---
  @include small-tablet {
    padding-top: 70px;
  }

  // --- max-width: 576px ---
  @include mobile {
    padding-top: 52px;
  }
}

.projects__title {
  @include section-title;
  margin-bottom: 14px;
  color: var(--dark-color);

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 17px;
  }
}

// --- BLOCK MODELS OF CARDS ---

.cards {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: var(--gap);

  // --- max-width: 1024px ---
  @include tablet {
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: 285px repeat(2, 307px);
    gap: 24px var(--gap);
  }

  // --- max-width: 768px ---
  @include small-tablet {
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(2, 300px) 307px 300px;
  }

  // --- max-width: 576px ---
  @include mobile {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }
}

.cards__item {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: start;
  min-height: 300px;

  // --- max-width: 1024px ---
  @include tablet {
    min-height: 285px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    min-height: 300px;
  }
}

.cards__item:nth-child(1) {
  grid-column: 1 / span 2;
}

.cards__item:nth-child(2) {
  grid-column: 3 / span 2;

  // --- max-width: 576px ---
  @include mobile {
    min-height: 278px;
  }
}

.cards__item:nth-child(3) {
  grid-column: 5 / span 2;

  // --- max-width: 1024px ---
  @include tablet {
    grid-column: 5 / span 4;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    grid-column: 5 / span 2;
  }

  // --- max-width: 576px ---
  @include mobile {
    min-height: 278px;
  }
}

.cards__item:nth-child(4) {
  grid-column: 7 / 13;
  background-image: url("../img/1024_projects-big-1.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  // --- max-width: 1024px ---
  @include tablet {
    grid-column: 1 / span 5;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    grid-column: 1 / span 6;
  }

  // --- max-width: 576px ---
  @include mobile {
    min-height: 422px;
    background-image: url("../img/320_projects-big-1.jpg");
  }
}

.cards__item:nth-child(5) {
  grid-column: 1 / 7;
  background-image: url("../img/1024_projects-big-2.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  // --- max-width: 1024px ---
  @include tablet {
    grid-column: 4 / span 5;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    grid-column: 1 / span 6;
    margin-top: 7px;
  }

  // --- max-width: 576px ---
  @include mobile {
    order: 1;
    gap: 8px;
    margin-top: 3px;
    min-height: 422px;
    background-image: url("../img/320_projects-big-2.jpg");
  }
}

.cards__item:nth-child(6) {
  grid-column: 7 / 10;

  // --- max-width: 1024px ---
  @include tablet {
    grid-column: 6 / span 3;
    grid-row: 2 / 3;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    grid-column: 1 / span 3;
    grid-row: 3 / 4;
    padding-top: 7px
  }

  // --- max-width: 576px ---
  @include mobile {
    min-height: 300px;
    padding-top: 2px;
  }
}

.cards__item:nth-child(7) {
  grid-column: 10 / 13;

  // --- max-width: 1024px ---
  @include tablet {
    grid-column: 1 / span 3;
    grid-row: 3 / 4;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    grid-column: 4 / span 3;
    grid-row: 3 / 4;
    padding-top: 7px
  }

  // --- max-width: 576px ---
  @include mobile {
    min-height: 300px;
    padding-top: 2px;
  }
}

// --- STYLES FOR CARDS ---

.cards__item-wrap {
  width: 100%;
}

.cards__img {
  width: 100%;
  margin-bottom: 18px;
}

.cards__descr {
  margin-bottom: 10px;
}

.cards__item:nth-child(6) .cards__descr,
.cards__item:nth-child(7) .cards__descr {

  // --- max-width: 768px ---
  @include small-tablet {
    margin-bottom: 17px;
  }

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 9px;
  }
}

.cards__descr--short {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.cards__item:nth-child(1) .cards__descr--short,
.cards__item:nth-child(2) .cards__descr--short,
.cards__item:nth-child(3) .cards__descr--short {

  // --- max-width: 1024px ---
  @include tablet {
    -webkit-line-clamp: 2;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    -webkit-line-clamp: 3;
  }
}

.cards__item:nth-child(2) .cards__descr--short,
.cards__item:nth-child(3) .cards__descr--short {

  // --- max-width: 768px ---
  @include small-tablet {
    -webkit-line-clamp: 2;
  }
}

.cards__link {
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: var(--dark-color);
  transition: color 0.5s ease-in-out,
    background-color 0.5s ease-in-out;
}

.cards__link:focus-visible {
  background-color: var(--link-text-focus);
}

@media (any-hover: hover) {
  .cards__link:hover {
    color: var(--orange-color);
  }
}

.cards__link:active {
  color: var(--orange-active-color);
}

.cards__link-btn {
  @include btn-empty;
}

.cards__time {
  display: inline-block;
  padding-left: 18px;
  font-size: 12px;
  font-weight: 400;
  line-height: 22px;
  color: var(--calendar-color);
  background-image: url("../img/calendar-icon.svg");
  background-repeat: no-repeat;
  background-position: left;
  background-size: 14px 14px;
}

.cards__time--main {
  // display: inline-block;
  background-image: url("../img/calendar-icon-main.svg");
  color: var(--light-color);

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 6px
  }
}

// --- CARDS' THEME LABEL ---

.cards__theme {
  @include theme-label;
  position: absolute;
  right: 6px;
  top: 7px;

  // --- max-width: 1024px ---
  @include tablet {
    padding: 6px 15px;
  }

  // --- max-width: 576px ---
  @include mobile {
    right: 5px;
    top: 4px;
    padding: 5px 14px;
  }
}

.cards__item:nth-child(6) .cards__theme,
.cards__item:nth-child(7) .cards__theme {

  // --- max-width: 768px ---
  @include small-tablet {
    top: 14px;
  }

  // --- max-width: 576px ---
  @include mobile {
    top: 7px;
  }
}

// --- MAIN CARDS ---

.cards__item--main {
  height: 100%;
  padding: 23px 30px 30px 30px;

  // --- max-width: 1024px ---
  @include tablet {
    padding: 31px 30px 30px 30px;
  }

  // --- max-width: 576px ---
  @include mobile {
  justify-content: flex-end;
  }
}

.cards__text-wrap {
  max-width: 292px;
}

.cards__subtitle {
  margin-bottom: 7px;
  font-family: var(--second-font-family);
  font-size: 24px;
  font-weight: 700;
  line-height: 24px;
  color: var(--light-color);

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 10px;
  }
}

.cards__descr--main {
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: var(--light-color);

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 15px;
    font-size: 14px;
    line-height: 129%;
  }
}

.cards__date--main {

  // --- max-width: 576px ---
  @include mobile {
    margin-bottom: 6px;
  }
}
