@mixin phone-link {
  display: inline-block;

  font-family: var(--second-font-family);
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  color: var(--orange-color);
  transform: translateY(-4px);
  transition: color 0.5s ease-in-out;

  // --- max-width: 1024px ---
  @include tablet {
    position: absolute;
    bottom: 22px;
    left: 173px;
  }

  // --- max-width: 960px ---
  @include in-between-tablet {
    position: unset;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    transform: translate(-5px, -4px);
  }

  // --- max-width: 576px ---
  @include mobile {
    font-size: 14px;
    font-weight: 500;
  }

  .phone__icon {
    width: 22px;
    height: 22px;
    fill: var(--orange-color);
    transform: translateY(6px);
    transition: fill 0.5s ease-in-out;

    // --- max-width: 1024px ---
    @include tablet {
      transform: translate(5px, 5px);
    }

    // --- max-width: 768px ---
    @include small-tablet {
      transform: translate(4px, 6px);
    }
  }

  &:focus-visible .phone__icon,
  &:focus-visible {
    fill: var(--orange-active-color);
    color: var(--orange-active-color);
  }

  @media (any-hover: hover) {

    &:hover .phone__icon,
    &:hover {
      fill: var(--brown-color);
      color: var(--brown-color);
    }
  }

  &:active .phone__icon,
  &:active {
    fill: var(--orange-active-color);
    color: var(--orange-active-color);
  }
}
