@mixin logo {
  display: inline-block;
  outline: 2px solid transparent;
  outline-offset: 5px;
  border-radius: 2px;

  &:focus-visible {
    outline-color: var(--light-color);
  }

  // --- max-width: 1024px ---
  @include tablet {
    width: 166px;
    height: 28px;
  }

  // --- max-width: 576px ---
  @include mobile {
    width: 112px;
    height: 18px;
    outline-offset: 2px;
  }
}
