.footer {
  background-color: var(--dark-color);
}

.footer__container {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: calc(var(--gap) / 2);
  padding-top: 23px;
  padding-bottom: 14px;

  // --- max-width: 1024px ---
  @include tablet {
    grid-template-columns: 3fr 5fr;
    padding-top: 24px;
    padding-bottom: 16px;
  }

  // --- max-width: 768px ---
  @include small-tablet {
    grid-template-columns: 1fr 1fr;
  }

  // --- max-width: 576px ---
  @include mobile {
    grid-template-columns: 1fr;
    padding-top: 31px;
    padding-bottom: 14px;
    // ---
    grid-template-rows: 1fr 1fr;
    padding-bottom: 0px;
    gap: 28px;
  }
}

// --- LOGO ---

.footer__logo-wrap {
  padding-top: 2px;

  // --- max-width: 1024px ---
  @include tablet {
    padding-top: 6px;
  }

  // --- max-width: 576px ---
  @include mobile {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-row: 2 / 3;
    background-color: var(--about-section-input);
    position: relative;
    left: 50%;
    right: 50%;
    margin-left: -50vw;
    margin-right: -50vw;
    width: 100vw;
    padding-bottom: 3px;
    padding-top: 0;
    min-height: 62px;
  }
}

.footer__logo {
  @include logo;
  margin-bottom: 1px;

  // --- max-width: 1024px ---
  @include tablet {
    margin-bottom: 0;
  }
}

.footer__copy {
  font-family: var(--second-font-family);
  font-size: 14px;
  font-weight: 400;
  color: var(--pale-color);

  // --- max-width: 576px ---
  @include mobile {
    font-size: 10px;
  }
}

// --- WRAPPER FOR QUOTE AND SOCIAL LINKS ---

.footer__wrap {
  display: flex;
  justify-content: space-between;
  gap: 10px;

  // --- max-width: 768px ---
  @include small-tablet {
    justify-content: flex-end;
  }

  // --- max-width: 576px ---
  @include mobile {
    justify-content: center;
  }
}

// --- QUOTE ---

.footer__quote-wrap {
  max-width: 361px;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: var(--about-section-text);

  // --- max-width: 768px ---
  @include small-tablet {
    display: none;
  }
}

.footer__quote {
  margin: 0;
}

// --- SOCIALS LINKS ---

.socials__subtitle {
  margin-bottom: 8px;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  text-align: center;
  color: var(--about-section-text);
}

.socials__list {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.socials__icon {
  width: 30px;
  height: 30px;
  fill: var(--about-section-text);
  transition: fill 0.5s ease-in-out;
}

.socials__link:focus-visible .socials__icon {
  fill: var(--brown-color);
}

@media (any-hover: hover) {
  .socials__link:hover .socials__icon {
    fill: var(--orange-color);
  }
}

.socials__link:active .socials__icon {
  fill: var(--orange-active-color);
}
